          "
        >
          <div class="balooda text-center">
            [বিঃদ্রঃ এখানে শুধুমাত্র অপার-চিন্ময় ২২ ব্যাচের এক্সামগুলো দেয়া যাবে
            এবং এক্সাম রিলেটেড সকল রিসোর্স পাওয়া যাবে। আমাদের সকল ক্লাস এবং
            লেকচার শীট একসাথে পাবার জন্যে এই বাটনে ক্লিক করোঃ
          </div>
          <div class="mt-5 text-center">
            <a
              href="https://buet-physics22.herokuapp.com"
              target="_blank"
              class="danger_btn mx-auto inline-block"
              tile
              large
              >Classes & Lecture Sheets</a
            >
          </div>
        </div>
        <div class="md:w-3/4 mx-auto">
          <div
            class="
              p-5
              bg-clip-padding
              backdrop-filter backdrop-blur-xl
              bg-opacity-30
              border border-gray-200
              bg-gray-200
              mx-3
              my-5
              rounded-lg
            "
            v-for="(e, i) in exams"
            :key="i"
          >
            <div class="text-center">
              <Timer
                :end="e.start"
                class="md:w-1/2 mx-auto mb-2"
                v-if="e.start > Date.now()"
              />
              <h1
                v-else-if="e.end > Date.now()"
                class="
                  text-center
                  mx-auto
                  white--text
                  my-4
                  text-white
                  font-bold
                  text-3xl
                "
              >
                Ongoing...
              </h1>

              <div>
                <h2
                  class="
                    ma-auto
                    balooda
                    text-2xl
                    font-semibold
                    text-gray-700
                    py-3
                  "
                >
                  {{ e.topic }}
                </h2>
              </div>
              <div class="text-lg">
                <h2 class="cursive text-xl text-gray-800 font-semibold">
                  Starting Time
                </h2>
                <h3 class="my-2 kalpurush">
                  {{ e.start | moment("Do, MMMM YYYY, h:mm a") }}
                </h3>
