                  class="danger_btn my-2 inline-block"
                  v-if="e.start < Date.now()"
                  :to="`/exam/${e.id}`"
                  >Start Exam</router-link
                >
                <button
                  class="danger_btn my-2 inline-block"
                  v-else
                  @click="upcomingalert"
                >
                  Start Exam
                </button>
