</template>

<script>
import Timer from "@/components/Timer.vue";
export default {
  components: { Timer },
  data() {
    return {
      exams: [],
    };
  },
  methods: {
    getRoutineInfo() {
      fetch(
        "https://script.google.com/macros/s/AKfycbwvwOv6OKjK8qTJdFcQF4v_AthQddTji1NEJhinBkD1XfvDggrYW8Dn4rBsXNNne61ktg/exec"
      )
        .then((res) => res.json())
        .then((data) => {
          this.exams = data.exams
            .map((r) => ({
              id: r.id.toString(),
              topic: r.topic,
              question: r.question,
              start: new Date(r.start).getTime(),
              end: new Date(r.end).getTime(),
              solve_sheet: r.solve_sheet,
            }))
            .reverse();
        });
    },
    endalert() {
      this.$swal({
        icon: "warning",
        title: "Will be available after the time ends",
      });
    },
    upcomingalert() {
      this.$swal({
        icon: "warning",
        title: "Upcomming",
        text: "Wait till starting Date.",
      });
    },
  },
  created() {
    this.getRoutineInfo();
  },
};
</script>

